:root {
  /* Spacing scale */
  --space-0: 0;
  --space-1: 0.25rem; /* 4px */
  --space-2: 0.5rem; /* 8px */
  --space-3: 0.75rem; /* 12px */
  --space-4: 1rem; /* 16px */
  --space-5: 1.125rem; /* 18px */
  --space-6: 1.25rem; /* 24px */
  --space-7: 1.5rem; /* 28px */
  --space-8: 2rem; /* 32px */
}
